:host {
  display: block;
  height: 100%;
}

// Main container - full viewport height, no scroll
.analytical-report-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

// Floating messages (error/success)
.floating-message {
  position: fixed;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  max-width: 90%;
  animation: slideDown 0.3s ease-out;

  &.error ::ng-deep .p-message {
    background-color: var(--red-50, #fef2f2);
    border-color: var(--red-200, #fecaca);
  }

  &.success ::ng-deep .p-message {
    background-color: var(--green-50, #f0fdf4);
    border-color: var(--green-200, #bbf7d0);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-1rem);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

// Chat View Layout - full height, 3-part structure
.chat-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;

  // Support [hidden] attribute (overrides display: flex)
  &[hidden] {
    display: none !important;
  }

  // Summary header is flex-shrink: 0 (stays fixed)
  app-chat-summary-header {
    flex-shrink: 0;
  }

  // Chat area fills remaining space and scrolls
  .chat-area {
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: var(--surface-ground, #f1f5f9);

    // WhatsApp-style chat background pattern
    background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23cbd5e1' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  }

  // Footer preview is flex-shrink: 0 (stays fixed)
  app-chat-footer-preview {
    flex-shrink: 0;
  }
}

// Dashboard View Layout
.dashboard-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;

  // Support [hidden] attribute (overrides display: flex)
  &[hidden] {
    display: none !important;
  }

  .dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 1rem;
    background-color: var(--card-background-color, #ffffff);
    border-bottom: 1px solid var(--input-border-color, #e2e8f0);
    flex-shrink: 0;

    .dashboard-actions {
      display: flex;
      gap: 0.5rem;
    }
  }

  app-analytics-dashboard {
    flex: 1;
    overflow-y: auto;
  }
}

// Loading Overlay
.loading-overlay {
  position: fixed;
  inset: 0;
  background-color: var(--mask-bg, rgba(255, 255, 255, 0.9));
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;

  .loading-content {
    text-align: center;

    .loading-message {
      margin-top: 1rem;
      font-size: 1rem;
      font-weight: 500;
      color: var(--color-primary-color, #1e40af);
    }

    .loading-hint {
      margin-top: 0.5rem;
      font-size: 0.875rem;
      color: var(--text-color-secondary, #64748b);
    }
  }
}

// Responsive adjustments
@media (max-width: 640px) {
  .dashboard-view .dashboard-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: stretch;

    .dashboard-actions {
      justify-content: flex-end;
    }
  }
}
